@font-face {
  font-family: 'Proxima Nova - Extrabld';
  src: url('fonts/ProximaNova-Extrabld.eot');
  src: url('fonts/ProximaNova-Extrabld.eot?#iefix') format('embedded-opentype'),
  url('fonts/ProximaNova-Extrabld.woff') format('woff'),
  url('fonts/ProximaNova-Extrabld.ttf') format('truetype');
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: 'Proxima Nova';
  src: url('fonts/ProximaNova-Regular.eot');
  src: url('fonts/ProximaNova-Regular.eot?#iefix') format('embedded-opentype'),
  url('fonts/ProximaNova-Regular.woff') format('woff'),
  url('fonts/ProximaNova-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

html, body {
  font-family: 'Proxima Nova', sans-serif;
  margin: 0;
  padding: 0;
}
.wow { visibility: hidden; }
a {
  color: #39b2f3;
}
p, li{
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 0.54px;
  line-height: 33px;
  &.description{
    padding-bottom: 30px;
  }
}
h1 {
  color: #000000;
  font-family: "Proxima Nova - Extrabld";
  font-size: 60px;
  font-weight: 400;
  line-height: 48px;
  padding-bottom: 30px;
}
h2 {
  color: #000000;
  font-size: 24px;
  font-weight: 700;
  letter-spacing: 0.72px;
  line-height: 33px;
}
ul {
  padding-left: 0;
  li {
    list-style-type: none;
    font-size: 18px;
    margin-bottom: 20px;
  }
  &.plus {
    li {
      padding-left: 22px;
      background: url("../images/liiconplus.png") no-repeat left 12px;
    }
  }
  &.minus {
    li {
      background: url("../images/liiconmin.png") no-repeat left 16px;
    }
  }
}

header{
  color: #fff;
  display: flex;
  background-color: #33b7f7;
  background-image: linear-gradient(to top, rgba(51, 181, 245, 0) 0%, #662584 100%),  url('../images/back.svg');
  background-position: top left, center left;
  background-repeat: no-repeat, no-repeat;
  background-size: 100% 100%, 45% 100%;
  height: 100vh;
  .logo {
    width: 161px;
    height: 28px;
    margin: 0px;
    display: inline-block;
    background: url('../images/logo-ex-2.svg');
  }
  .endless {
    width: 340px;
  }
  .signup, .whitepaper {
    //margin-left: 15px;
    background: transparent;
    color: #fff;
    transition: All 0.2s ease-out;
    cursor: pointer;
    margin-top: 35px;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1.4px;
    padding: 19px 62px;
    border: 1px solid #fff;
    border-radius: 14px;
    width: 200px;
    text-align: center;
    &:hover{
      background: #fff;
      color: #3ab3f3;
      text-decoration: none;
    }

  }
  .whitepaper {
    background: #fff;
    color: #3ab3f3;
    &:hover{
      background: transparent;
      color: #fff;
      text-decoration: none;
    }
  }
  h1{
    color: #fff;
    line-height: 0;
  }
  p {
    font-size: 24px;
    margin-bottom: 40px;
  }
  .nav.general {
    .nav-item {
      position: relative;

      z-index: 1;
      .nav-link {
        -webkit-transition: all 0.5s;
        transition: all 0.5s;
        //background: #4e469c;
        padding-top: 2px;
        padding-bottom: 0;
        line-height: 18px;
      }
      &:hover .nav-link {
        color: #3ab3f3;
      }
    }

    $rotate: -80deg;
    .nav-item {
      &::before, &::after {
        content: '';
        position: absolute;
        height: 2px;
        width: 30px;
        top: 40%;
        margin-top: -25px;
        z-index: -1;
        background: #F9F9F9;
        -webkit-transform: rotate3d(0, 0, 1, $rotate) scale3d(0, 1, 1);
        transform: rotate3d(0, 0, 1, $rotate) scale3d(0, 1, 1);
        -webkit-transition: -webkit-transform 0.5s;
        transition: transform 0.5s;
      }
      &::after {
        margin-top: 28px;
      }

      &.product {
        &::after {
          left: 24%;
        }
        &::before {
          left: 35%;
        }
      }
      &.crowd {
        &::before {
          left: 40%;
        }
        &::after {
          left: 31%;
        }

      }
      &.blog {
        &::before {
          left: 35%;
        }
        &::after {
          left: 21%;
        }
      }
      &.faq {
        &::before {
          left: 30%;
        }
        &::after {
          left: 16%;
        }
      }
      &.whiteppr {
        &::before {
          left: 40%;
        }
        &::after {
          left: 32%;
        }
      }

      &.active::before, &.active::after{
        -webkit-transform: rotate3d(0,0,1,$rotate) scale3d(1,1,1);
        transform: rotate3d(0,0,1,$rotate) scale3d(1,1,1);
        background: #fff;
      }
      &:hover::after, &:hover::before  {
        background: #3ab3f3;
        -webkit-transform: rotate3d(0,0,1,$rotate) scale3d(1,1,1);
        transform: rotate3d(0,0,1,$rotate) scale3d(1,1,1);
      }
    }

  }
}
footer {
  background-image: linear-gradient(to top, #862182 0%, #662786 100%);
  p{
    color: #fff;
  }
  .sc{
    color: #fff;
    width: 20%;
    font-size: 14px;
    font-weight: 400;
    line-height: 33px;
    letter-spacing: 0.42px;
    padding-bottom: 0px;
    &:hover {
      text-decoration: none;
    }
  }
  .container {
    padding: 60px 0 30px;
  }
}
