    }
  }
  &.roadmap {
    .container {
      padding-bottom: 50px;
    }
    h2 {
      color: #33b7f7;
      font-size: 36px;
      font-weight: 700;
      line-height: 33px;
      text-transform: uppercase;
      letter-spacing: 3.6px;
      position: relative;
      overflow: hidden;
      &.double {
        margin-top: -32px;
      }
      &::after {
        content: '';
        width: 115px;
        height: 2px;
        background: #33b7f7;
        display: block;
        position: absolute;
        bottom: 7px;
        right: 7px;
      }
    }
    p{
      font-size: 18px;
      font-weight: 400;
      line-height: 30px;
    }
    img {
      margin-bottom: 20px;
    }
    .owl-carousel {
      &.owl-drag .owl-item {
        padding-top: 35px;
        p {
          width: 200px;
        }
      }
      .owl-nav {
          width: 100%;
        .owl-next, .owl-prev {
          color: transparent;
          position: absolute;
          top: 20%;
        }
        .owl-next {
          position:absolute;
          right: -100px;

          background: url("../images/arright.png");
          width: 123px;
          height: 123px;
        }
        .owl-prev {
          position:absolute;
          left: -80px;

          background: url("../images/arleft.png");
          width: 123px;
          height: 123px;
        }
      }
    }
  }
  &.team {
    h4 {
      color: #62318d;
      font-weight: 600;
      letter-spacing: 1.8px;
      text-transform: uppercase;
      font-size: 18px;
    }
    h5{
      font-size: 24px;
      font-weight: 700;
      letter-spacing: 0.72px;

    }
    p {
      font-size: 18px;
      font-weight: 400;
      line-height: 26px;
      margin-bottom: 0px;
    }
    .icon2 {
      display: inline-block;
      width: 26px;
      height: 26px;
      &.linkedIn {
        background: url(../images/in.png);
        background-size: contain;
      }
      &.github {
        background: url(../images/github.svg);
        background-size: contain;
      }
      &.behance {
        background: url(../images/be.svg);
        background-size: contain;
      }
      &.springer {
        background: url(../images/springerlink.png) no-repeat;
        background-size: contain;
      }
      &.dblp {
        background: url(../images/dblp.png) no-repeat;
        background-size: contain;
      }
      &.sc {
        background: url(../images/sc.ico) no-repeat;
        background-size: contain;
      }
      &.rg {
        background: url(../images/rg.png);
        background-size: contain;
      }
    }
    .photo {
      width: 155px;
      position: relative;
      margin-bottom: 25px;
      &::after {
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        background: url("../images/vline.png") no-repeat;
        height: 141px;
        width: 35px
      }
    }
    .vmiddle {
      height: auto;
    }
    .nav-tabs {
      border: 0;
      margin-bottom: 50px;
      .nav-item {
        background: transparent;
        border: 0;
        color: #39b2f3;
        font-size: 14px;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 0.56px;
        &.active {
